.iw-home-introduce--container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 0 50px;
}

.iw-home-introduce--text {
  white-space: pre-wrap;
  color: var(--main-font-color);
  font-size: 32px;
}

.iw-home-introduce--image {
  width: 350px;
  margin-left: 50px;
}

.iw-home-introduce--image img {
  width: 100%;
  border-radius: 35px;
}
